
import base from "../../components/request/baseUrl.js";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
data() {
return {
roomArr: [], //
topListArr: [
{ name: "" },
{ name: "" },
{ name: "" }
], //
sortArr: [
{ name: "" },
{ name: "" },
{ name: "" }
], //
titleArr: [
{ name: "" },
{ name: "" },
{ name: "" },
{ name: "" }
], //Arr
doctorListArr: [], //

roomShow: false, //
roomListShow: false, //
sortListShow: false, //
titleListShow: false, //
titleIconShow: true, //
roomIconShow: true,       sortIconShow: true,       isActive: -1, //
listActive: 0, //
titleActive: 3,       roomName: "", //
sortName: "",
titleName: "",
selectRoomName: "", //
selectDoctorName: "", //
selectSortName: "score"
};
},
methods: {

getRoomList() {
base
.get("/api/drug/department/")
.then(res => {
console.log(res);
if (res.data.status == 200201) {
this.roomArr = res.data.msg;
this.roomArr.push({ name: "" });
if (JSON.stringify(this.$route.query) == "{}") {
this.roomName = "";
this.selectRoomName = "";
this.isActive = this.roomArr.length - 1;
this.getDoctorList();
} else {
this.selectRoomName = this.$route.query.room;
this.isActive = this.$route.query.roomId - 1;
this.roomName = this.$route.query.room;
this.getDoctorList();
}
} else {
console.log("");
}
})
.catch(err => {
console.log(err);
});
},

roomBtn() {
this.roomShow = true;
this.roomListShow = true;
this.sortListShow = false;
this.titleListShow = false;
this.roomIconShow = false;
this.sortIconShow = true;
this.titleIconShow = true;
},
sortBtn() {
this.roomShow = true;
this.sortListShow = true;
this.roomListShow = false;
this.titleListShow = false;
this.sortIconShow = false;
this.titleIconShow = true;
this.roomIconShow = true;
},
titleBtn() {
this.roomShow = true;
this.titleListShow = true;
this.sortListShow = false;
this.roomListShow = false;
this.titleIconShow = false;
this.roomIconShow = true;
this.sortIconShow = true;
},


getDoctorList() {
this.$vux.loading.show({
text: "Loading"
});
base
.get(
"/api/app/doctor/?department=" +
this.selectRoomName +
"&professional_title=" +
this.selectDoctorName +
"&sort_by=" +
this.selectSortName
)
.then(res => {
console.log(res, "");
this.$vux.loading.hide();
if (res.data.status == 200001) {
this.doctorListArr = res.data.msg;
this.roomShow = false;
} else {
console.log("");
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


doctorConsultBtn(index, item) {
localStorage.setItem("doctorId", item.id);
this.$router.push({
path: "doctorDetails"
});
},


selectRoom(index, item) {
this.roomShow = true; 

this.roomName = item.name;
this.isActive = index;
if (item.name == "") {
this.selectRoomName = "";
this.getDoctorList();
} else {
this.selectRoomName = item.name;
this.getDoctorList();
}
},






selectSort(index, item) {
this.roomShow = true; 
this.listActive = index;
this.sortName = item.name;
if (item.name == "") {
this.selectSortName == "fans";
this.getDoctorList();
} else {
this.selectSortName = "score";
this.getDoctorList();
}
},
selectTitle(index, item) {
this.titleName = item.name;
this.roomShow = true; 
this.titleActive = index;
this.selectDoctorName = item.name;
this.getDoctorList();
},


preDefault() {
this.roomShow = false;
}
},
mounted() {
this.getRoomList(); 









},
components: {
Flexbox,
FlexboxItem
}
};



