import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      roomArr: [], //
      listArr: [],//
      roomId: 0,//ID
      pageNum: 1,//
      pageSizeNum: 10,//
      isActive: 0,//id
    };
  },
  methods: {
    // 
    detailsBtn() {
      // this.$router.push({
      //   path: "findListDetails"
      // });
    },

    // 
    selectRoom(id){
      this.isActive=id;
      this.roomId = id;
      this.getList();
    },



    // 
    getRoom() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/drug/department/")
        .then(res => {
          this.$vux.loading.hide();
          console.log(res);
          if (res.data.status == 200201) {
            this.roomArr = res.data.msg;
          } else {
            console.log("");
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    },

    // 
    getList(){
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/drug/disease/" + this.roomId + '/?page=' + this.pageNum + '&page_size=' + this.pageSizeNum)
        .then(res => {
          this.$vux.loading.hide();
          console.log(res);
          if(res.data.status == 200001){
            this.listArr = res.data.msg;
          }else {
            console.log("")
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    },


    // ID
    getItemId(){
      
    }
  },
  mounted() {
    if(this.$route.query.id != undefined){
      this.isActive = this.$route.query.id;
      this.roomId = this.$route.query.id;
    }

    this.getRoom(); //

    this.getList();//
    console.log(this.$route.query)
  }
};



