import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      problemList: [] //
    };
  },
  methods: {
    //   
    detailsBtn(index,item) {
      this.$router.push({
        path: "newProblemDetails",
        query: {
          questionId: item.id
        }
      });
    },

    // 
    getnewProblem() {
      base
        .get("/api/app/consult/")
        .then(res => {
          console.log(res,"");
          if(res.data.status == 200001){
              this.problemList = res.data.msg;
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },

  mounted() {
    this.getnewProblem(); //
  }
};



