import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      leftListArr: [], //
      rightListArr: [], //
      listArr: [], //
      isActive: 0, //
      rightActive: 0, //
      num: "00", //
      popShow: true, //
      name: "" //
    };
  },
  methods: {
    searchFocus() {
      this.$router.push({
        path: "findSearch"
      });
    },

    selectBtn() {
      this.popShow = true;
      this.getDetailsList(); //
    },

    // 
    getClassifyList() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/drug/solution_category/")
        .then(res => {
          this.$vux.loading.hide();
          console.log(res);
          if (res.data.status == 200001) {
            this.leftListArr = res.data.msg;
          } else {
            console.log("");
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
        });
    },

    // 
    getDetailsList() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/drug/solution_category/" + this.num + "/")
        .then(res => {
          console.log(res);
          this.$vux.loading.hide();
          if (res.data.status == 200001) {
            this.rightListArr = res.data.msg;
            this.name = res.data.msg[0].sub_name_2;
          } else {
            console.log("");
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    },

    // 
    selectClassify(index, item) {
      this.isActive = index;
      this.num = item.sub_code_1;
      this.getDetailsList(); //num
    },

    //
    selectItem(index, item) {
      this.rightActive = index;
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/drug/drug/?search_type=solution_code/" + item.sub_code)
        .then(res => {
          console.log(res);
          this.popShow = false;
          this.$vux.loading.hide();
          if (res.data.status == 200001) {
            this.listArr = res.data.msg;
          } else {
            console.log("");
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
        });
    },

    // 
    listDetails(index, item) {
      this.$router.push({
        path: "findListDetails",
        query: {
          id: item.id
        }
      });
    }
  },
  mounted() {
    this.getClassifyList(); //
    this.getDetailsList();
  }
};



