
import base from '../../components/request/baseUrl.js';
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name: "my",
data() {
return {
msg: "Welcome to Your Vue.js App",
userInfo: {},      realName: '',//
nickName: '',
avatar: '',
};
},
methods: {
getUserInfo(){
base.get('/api/auth/user/profile/')
.then(res=>{
console.log(res,"");
if(res.data.status == 201002){
if(res.data.msg.avatar == undefined || res.data.msg.avatar == null || res.data.msg.avatar == ''){
this.avatar = require('../../assets/advisory-bg-photo-.png');
}else {
this.avatar = res.data.msg.avatar;
};
if(res.data.msg.nickname == undefined || res.data.msg.nickname == null || res.data.msg.nickname == ''){
this.nickName = '';
}else {
this.nickName = res.data.msg.nickname;
};
if(res.data.msg.real_name == undefined || res.data.msg.real_name == null || res.data.msg.real_name == ''){
this.realName = "";
}else {
this.realName = res.data.msg.real_name;
}
}else {
console.log("");
}
}).catch(err=>{
console.log(err)
});
}
},
mounted() {
this.getUserInfo();
this.userInfo = this.$store.state.userInfo;
},
components: {
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem
}
};



